/* CUSTOM CSS BEGINS HERE */

.rarity-ultra-rare {
  background: linear-gradient(120deg, #fbffca, #baffbf, #a7efff, #ffabff);
}
.rarity-decisive {
  background: linear-gradient(120deg, #fbffca, #baffbf, #a7efff, #ffabff);
}
.rarity-priority {
  background: #ee9;
}
.rarity-super-rare {
  background: #ee9;
}
.rarity-elite {
  background: #c4adff;
}
.rarity-rare {
  background: #9fe8ff;
}
.rarity-common {
  background: #ccc;
}
.ship-rarity-padding {
  padding: 4px;
}
.eq-rarity-padding {
  border: 4px solid grey;
}

.tooltip.show {
  opacity: 1;
}

.sub-page-background {
  background-repeat: no-repeat;
  background-size: cover;
  min-height: calc(100vh - 62px);
}

/* CUSTOM CSS ENDS HERE */

body {
  color: $black-color;
  font-size: $font-size-base;
  font-family: $sans-serif-family;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

.main {
  position: relative;
  background: $white-color;
}
/* Animations */
.nav-pills .nav-link,
.nav-item .nav-link,
.navbar,
.nav-tabs .nav-link,
.tag,
.tag [data-role="remove"] {
  @include transition($general-transition-time, $transition-ease);
}

//transition for dropdown caret
.card a,
.bootstrap-switch-label:before {
  @include transition($fast-transition-time, $transition-ease);
}

.dropdown-toggle:after,
[data-toggle="collapse"][data-parent="#accordion"] i {
  @include transition-on-prop(
    transform,
    $fast-transition-time,
    $transition-ease
  );
}

.dropdown-toggle[aria-expanded="true"]:after,
[data-toggle="collapse"][data-parent="#accordion"][aria-expanded="true"] i {
  @include rotate-180();
}

.button-bar {
  display: block;
  position: relative;
  width: 22px;
  height: 1px;
  border-radius: 1px;
  background: $white-bg;

  & + .button-bar {
    margin-top: 7px;
  }

  &:nth-child(2) {
    width: 17px;
  }
}

.separator-line {
  height: 2px;
  width: 44px;
  background-color: $default-color;
  margin: 20px auto;

  &.separator-primary {
    background-color: $primary-color;
  }
}

.section-space {
  height: 62px;
  display: block;
}

.pull-left {
  float: left;
}
.pull-right {
  float: right;
}

.title-up {
  text-transform: uppercase;
}
